// Fast Click for touch devices
$(function() {
	FastClick.attach(document.body);
});

// Init Sockets
var server = io.connect('http://127.0.0.1:8080');

server.on('connect', function (d) {
	console.log(d);
	server.send('hi');
	server.emit('hello', 'world', function (data) {
		console.log(data);
	});
});

// Init Touch Controls
var control_left = nipplejs.create({
	zone: document.getElementById('control-left'),
	mode: 'static',
	position: {
		left: '50%',
		top: '50%'
	},
	size: 100,
	color: 'black'
});
var control_right = nipplejs.create({
	zone: document.getElementById('control-right'),
	mode: 'static',
	position: {
		left: '50%',
		top: '50%'
	},
	size: 100,
	color: 'black'
});
